.page-footer__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin: 14px auto;
  font-size: 13px;
  line-height: 24px;
  align-items: center;
  text-align: right;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-align: center;
  color: $gray-darkest;
  opacity: 0.5;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: center;
    margin: 0;
  }
}

.page-footer__wrapper-copyright {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  margin: 0;
  margin-right: 143px;

  @media (max-width: $desktop-width-max) {
    display: none;
  }

  @media (max-width: $tablet-width) {

  }
}

.page-footer__wrapper-copyright span {
  margin-right: 69px;
}

.page-footer__wrapper a {
  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 21px;
  }
}

.page-footer__wrapper-author {
  margin: 0;
  margin-left: auto;

  &:hover,
  &:focus {
    opacity: 1;
  }

  &:active {
    opacity: 0.6;
  }

  @media (max-width: $tablet-width) {
    margin: 0 auto;
  }
}

